@charset "UTF-8";
/*Обнуление*/
* {
  padding: 0;
  margin: 0;
  border: 0; }

*, *:before, *:after {
  box-sizing: border-box; }

:focus, :active {
  outline: none; }

a:focus, a:active {
  outline: none; }

nav, footer, header, aside {
  display: block; }

html, body {
  height: 100%;
  width: 100%;
  /*font-size: 100%;*/
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

input, button, textarea {
  font-family: inherit; }

input::-ms-clear {
  display: none; }

button {
  cursor: pointer; }

button::-moz-focus-inner {
  padding: 0;
  border: 0; }

a, a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

ul li {
  list-style: none; }

img {
  vertical-align: top; }

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit; }
/*--------------------*/

body {
  background: #f3f6f8;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*font-family: "Trebuchet MS";*/

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*--------------------*/
.ibg {
    position: relative; }
.ibg img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover; }

body.ie .ibg {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat; }

body.ie .ibg img {
    width: 0;
    height: 0;
    opacity: 0;
    visibility: hidden; }

body.lock {
    overflow: hidden; }

.container {
    max-width: 1030px;
    padding: 0 15px;
    margin: 0 auto;
    min-height: 100%;
    flex: 1 1 auto;
}

.app-wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    min-width: 100%;
    overflow: hidden; }

.app-wrapper-content {
    display: flex;
    flex: 1 1 auto;
    background: #f3f6f8 url(data:image/png;base64,) repeat-x 50% 0;

}

.content {
    padding: 20px 0px;
    display: flex;
    min-height: 100%;
    justify-content: center;
}
.contentLayout {
